.form-group { 
	color: $primary; 
	padding-top: 1.3rem;
	font-weight: 600;

	.selector {
		margin-right: 0.2rem;
	}

	select {
		color: $gray-600;
		padding-top: 0.8rem;
		padding-bottom: 0.8rem;
		margin-left: 0.3rem;
	}
	option {
		color: $gray-600;
	}

	.form-control {
		background-color: $gray-100;
		box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
		padding-top: 0.8rem;
		font-size: 1.5rem;
		color: $gray-600;
	}
	.row-selector {
		box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	}
}