/*
 * Styles in this file relate to the template file: /templates/content-archive-listing.php
 */

&-archive {
	font-size: 1.5rem;
	padding-top: 2rem;       
	
	h3 {
		padding-top: 1rem;
		font-size: 5;
		color: $black;
		text-transform: uppercase;
		font-weight: bold;
		margin-bottom: 2rem;
	}
	.list {       
	   
		.item { 
			box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
			border-radius: 10px;
			transition: transform .2s;
			margin-bottom: 4rem; 
			padding: 1rem;  

			&:hover {
				transform: scale(1.005);
				box-shadow: 
					rgba(0, 0, 0, 0.25) 0px 54px 55px, 
					rgba(0, 0, 0, 0.12) 0px -12px 30px, 
					rgba(0, 0, 0, 0.12) 0px 4px 6px, 
					rgba(0, 0, 0, 0.17) 0px 12px 13px, 
					rgba(0, 0, 0, 0.09) 0px -3px 5px;

				button {
					box-shadow: 
						rgba(0, 0, 0, 0.25) 0px 54px 55px, 
						rgba(0, 0, 0, 0.12) 0px -12px 30px, 
						rgba(0, 0, 0, 0.12) 0px 4px 6px, 
						rgba(0, 0, 0, 0.17) 0px 12px 13px, 
						rgba(0, 0, 0, 0.09) 0px -3px 5px;
				}
			} 
			
			.image {
				position: relative;

				.background-image {
					display: block;
					position: relative;
					z-index: 0;
					background-repeat: no-repeat;
					background-size: cover;
					background-position: 50%;
					overflow: hidden;
					width: 100%;
					min-height: 300px;
					height: 100%;

					img {
						display: none;
					}
				} 

				.media-icons {
					position: absolute;
					bottom: 1rem;
					left: 2rem;
					display: flex;

					.photos,
					.videos { 
						svg {
							stroke: $primary;
							width: 1rem;
							height: 1rem; 
						}

						color: $primary;
						font-size: 1rem;
						font-weight: 600;
						padding: 3px 10px;
						background-color: rgba($gray-200, 0.9);
						margin-right: 10px;                                                
						border-radius: 5px;
						text-align: center;                        
					}
				}

				.kind{
					position: absolute;
					left: 2rem;
					top: 1rem;

					a {
						text-decoration: none;
						text-transform: uppercase;
						color: $white;
						font-size: 1rem;
						font-weight: 600;
						padding: 3px 10px;
						background-color: rgba($black, 0.9);  
						border-radius: 5px;
						display: block;
					}
				}
			}
			 
			.info { 
				.price-title {  
					display: flex;
					justify-content: space-between;
					align-items: center;

					.title {
						text-decoration: none;
						font-size: 1.5rem;
						color: $black; 
						font-weight: 600;                           
					}
					.price{
						color: $secondary;
						text-align: right; 
						font-weight: 800;
						font-size: 1rem;
					}
				}
				 
				.additional-info{
					
					text-align: left;
					padding-bottom: 1rem;
					
					ul {
						list-style: none;
						padding: 0;
						display: flex;
						flex-wrap: wrap; 

						li {
							padding-bottom: 10px;
							font-size: 1rem;
							width: 50%;

							strong { 
								color: $primary; 
								margin-right: 5px;
							}
						}
					} 
				}
			}
			
			.more-info {       
				text-align: right;     
			} 
		}           
	}        
}

@media ( max-width: 767px ) {
	.list .item .info .additional-info ul {
		display: block;
	}
    .owl-item {
		opacity: 1 !important;
	}
}