/*
 * Styles in this file relate to the template file: /templates/content-single-listing.php
 */

//CAROUSEL SECTION
&-carousel { 
	width: 100vw;
	position: relative;
	margin-left: -50vw;  
	left: 50%;  

	.owl-nav {
		position: absolute;
		top: 50%;
		right: 1rem;
		left: 1rem;

		button {
			position: absolute;
			background: $primary !important;
			border-radius: 50px;
			width: 2rem;
			height: 2rem;

			i {
				color: #fff;
			}
		}

		.owl-prev {
			left: 1rem;
		}

		.owl-next {
			right: 1rem;
		}
	}

	.owl-item {
		opacity: .5;

		a {
			height: 650px;
			width: 1120px;
			background-color: #efefef;
			background-size: cover !important;
			background-repeat: no-repeat; 
			background-position: 50%;
			display: inline-block;
			border-radius: 10px;

			&.portrait-item {
				background-size: auto 100% !important;
			}
		}

		&.center { 
			opacity: 1; 
		}
	}

	.background-image img {
		display: none !important;
	}
}

//INFO SECTION
&-info {
	.price {
		padding-top: 2rem; 
		color: $secondary;
		font-size: 2rem;
	} 
	.title {
		font-size: 2rem;
		color: black;
		font-weight: bold;
		margin-top: 1rem;
	}

	&-icons {
		padding-top: 0.2rem;
		stroke: $primary;

		.social-icon {
			padding-left: .2rem;
			width: 2rem;
			display: inline-block;
		}
		.social-icon:hover {
			stroke: $secondary;
		}
	}

	.listing-buttons {
		padding-top: 2rem;
  
	} 
}

//GENERAL INFORMATION SECTION
&-gen-info {
	padding-top: 2rem; 

	.broker {
		padding-top: 2rem;
		font-size: 1.3rem;

		a {
			color: $primary;
			text-decoration: none;
		}
		a:hover {
			color: $secondary;
		}

		&-name {
			font-weight: bold;
		}
	}
	.long-description-section,
	.long-description-section p { 
		font-size: 1rem;  
	}

	.head {
		padding-top: 1rem;
		font-size: 5;
		color: $black;
		text-transform: uppercase;
		font-weight: bold;
	}

	.grid-container { 
		display: grid;
		grid-template-columns: auto auto auto;
		grid-template-rows: auto auto;

		.item.boat-info-item.item-title {
			margin-bottom: 20px;
		}

		strong {
			text-transform: uppercase;
			color: $primary;
		}
	}
}

//ACCORDION SECTION
&-accordion {
	font-size: 1rem;
 
	.accordion-button {
		font-size: 1.5rem;
		background-color: transparent;
		text-transform: uppercase;
		color: $primary;
		font-weight: bold;
	}
	.accordion-button:hover {
		background-color: transparent;
		color: $secondary;
	}

	.grid-container {
		padding: 1rem 1.25rem;  
	} 
}

//CONTACT SECTION
&-contact {
	font-size: 1rem;
	margin-top: 2rem;

	h2 {
		padding-top: 1rem;
		font-size: 5;
		color: $black;
		text-transform: uppercase;
		font-weight: bold;
	} 
	.head {
		padding-top: 1rem;
		font-size: 5;
		color: $black;
		text-transform: uppercase;
		font-weight: bold;
	}

	.form-check {
		margin-top: 1rem;
		margin-bottom: 2rem;
		margin-left: 1rem;
		color: black;
		font-weight: normal;
		font-size: 1rem;

		a {
			text-decoration: none;
		}
	}
	
	.form-check-input {
		margin-left: 0;
	}

	.form-check-label {
		margin-left: 1.5rem;
	}

	select {
		display: block;
	}

	.btn-send-enquiry {
		min-width: 200px
	}
 
}

&-related {
	font-size: 1.5rem;
	padding-top: 2rem;

	h2 {
		padding-top: 1rem;
		font-size: 5;
		color: $black;
		text-transform: uppercase;
		font-weight: bold;
		margin-bottom: 2rem;
	}
	.dlmarine-similar-listings { 

		.col-sm-12 {
			margin-bottom: 40px;
		}

		.item-similar-listing {
			box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
			border-radius: 10px;
			transition: transform 0.2s;
			position: relative;
			height: 100%;
			display: flex;
    		flex-direction: column;
			
  
			.image {
				position: relative;

				.background-image {
					display: block;
					position: relative;
					z-index: 0;
					background-repeat: no-repeat;
					background-size: cover;
					background-position: 50%;
					overflow: hidden;
					width: 100%; 
					height: 250px;
					border-radius: 10px 10px 0 0;

					img {
						display: none;
					}
				} 

				.media-icons {
					display: flex;
					position: absolute;
					bottom: 1rem;
					left: 1rem;
	
					.photos,
					.videos { 
						svg {
							stroke: $primary;
							width: 1rem;
							height: 1rem; 
						}
	
						color: $primary;
						font-size: 1rem;
						font-weight: 600;
						padding: 3px 10px;
						background-color: rgba($gray-200, 0.9);
						margin-right: 10px;                                                
						border-radius: 5px;
						text-align: center;                        
					}
				}
	
				.kind {
					position: absolute;
					left: 1rem;
					top: 1rem; 
					text-decoration: none;
					text-transform: uppercase;
					color: $white;
					font-size: 1rem;
					font-weight: 600;
					padding: 3px 10px;
					background-color: rgba($black, 0.9);  
					border-radius: 5px;
					display: block;
				}
			}

			.additional-info {
				padding: 1rem; 

				.title {
					text-decoration: none;
					text-transform: uppercase;
					font-size: 1.5rem;
					font-weight: bold; 
					color: $primary;
				}

				.price-small {  
					font-size: 1rem;
				}

				ul {  
					padding: 0;
					margin-top: 10px;
					li {
						font-size: 1rem;
						list-style: none; 
					}
					strong {
						text-transform: uppercase;
						color: $primary;
						
					}
				}
			}
			.more-info { 
				margin: 0 1rem 1rem; 
				margin-top: auto;
			} 
		} 
	}
}   